import api from '@wordpress/api'
import { Button, Icon, Panel, PanelBody, PanelRow, Placeholder, SelectControl, Spinner, TextControl, ToggleControl } from '@wordpress/components'
import { dispatch, useDispatch, useSelect } from '@wordpress/data'
import { Fragment, render, Component } from '@wordpress/element'
import { __ } from '@wordpress/i18n'
import { store as noticesStore } from '@wordpress/notices'
import { Field } from './field.js'

console.log(webaxonesData);
const Notices = () => {
    const notices = useSelect(
        ( select ) =>
            select( noticesStore )
                .getNotices()
                .filter( ( notice ) => notice.type === 'snackbar' ),
        []
    );
    const { removeNotice } = useDispatch( noticesStore );
    return (
        <SnackbarList
            className="edit-site-notices"
            notices={ notices }
            onRemove={ removeNotice }
        />
    )
}

class App extends Component {
    constructor() {
        super( ...arguments )

		this.state = {}

		webaxonesData.forEach( field => {
			this.state[field.slug] = ''
		} )

		this.state['isAPILoaded'] = false
    }

	componentDidMount() {

        api.loadPromise.then( () => {
            this.settings = new api.models.Settings()

            const { isAPILoaded } = this.state

            if ( isAPILoaded === false ) {
                this.settings.fetch().then( ( response ) => {
					webaxonesData.forEach( field => {
						this.setState( {
							[field.slug]: response[ field.slug ],
						} )
					} )
                } )
            }
        } )
    }

    render() {
		console.log(this.state);
		// if ( ! this.state.isAPILoaded ) {
        //     return (
        //         <Placeholder>
        //             <Spinner />
        //         </Placeholder>
        //     );
        // }

        return (
            <Fragment>
				{ webaxonesData.map( data => {
					return <Field field={data} />
				} ) }
			</Fragment>
        )
    }
}

document.addEventListener( 'DOMContentLoaded', () => {
    const htmlOutput = document.getElementById( 'wax-company-settings__content' )
    if ( htmlOutput ) {
        render(
            <App />,
            htmlOutput
        );
    }
});
