import { Button, Icon, Panel, PanelBody, PanelRow, Placeholder, SelectControl, Spinner, TextControl, ToggleControl } from '@wordpress/components'
import { Fragment, render, Component } from '@wordpress/element'
import { __ } from '@wordpress/i18n'

export const Field = ( { field } ) => {
	return (
		<TextControl
			help={ field.setting_help }
			label={ field.setting_label }
			onChange={ ( field ) => this.setState( [field.slug] ) }
			value={ field.slug }
		/>
	)
}